import { CheckCircleOutlined } from '@ant-design/icons';
import { Card, Image, Typography } from 'antd';
import React from 'react';
import styled from 'styled-components';
import Button from '../shared/Button';
import ShareButtons from '../shared/ShareButtons';
import { CardBase, CardContent, Container, GoddessImage } from '../shared/styles';

const { Title, Text, Paragraph } = Typography;

const ResultCard = styled(CardBase)`
  .ant-card-body {
    padding: 0;
  }
`;

const IconWrapper = styled.div`
  font-size: 48px;
  color: #52c41a;
  margin-bottom: 24px;
`;

const ResultTitle = styled(Title)`
  margin-bottom: 16px !important;
`;

const ResultSummary = styled(Text)`
  display: block;
  font-size: 18px;
  color: #8c8c8c;
  margin-bottom: 32px;
`;

const AnimeImage = styled(Image)`
  max-width: 300px;
  width: 100%;
  border-radius: 8px;
  margin-bottom: 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
`;

const AnimeTitle = styled(Title)`
  color: #1890ff !important;
  margin-bottom: 16px !important;
`;

const AnimeDescription = styled(Paragraph)`
  color: #595959;
  font-size: 16px;
  margin-bottom: 32px !important;

  &.ant-typography {
    margin: 16px 0 32px;
  }
`;

const ButtonWrapper = styled.div`
  margin-top: 32px;
`;

const CardContainer = styled.div`
  position: relative;
  width: 100%;
  max-width: 800px;
  margin-top: 400px;
  margin-bottom: 100px;
`;

const ResultScreen = ({ anime, questionCount, onShare, onRestart }) => {
  const baseUrl = process.env.PUBLIC_URL;

  return (
    <Container>
      <CardContainer>
        <ResultCard as={Card}>
          <CardContent>
            <IconWrapper>
              <CheckCircleOutlined />
            </IconWrapper>

            <ResultTitle level={2}>診断結果</ResultTitle>

            <ResultSummary>{questionCount}問の質問で結果が出ました！</ResultSummary>

            <AnimeImage
              src={anime.imageUrl}
              alt={anime.title}
              fallback="data:image/png;base64,"
            />

            <AnimeTitle level={3}>{anime.title}</AnimeTitle>

            <AnimeDescription>{anime.description}</AnimeDescription>

            <ShareButtons onShare={onShare} />

            <ButtonWrapper>
              <Button type="primary" size="large" onClick={onRestart}>
                もう一度診断する
              </Button>
            </ButtonWrapper>
          </CardContent>
          <GoddessImage src={`${baseUrl}/images/smile.png`} alt="笑顔の女神" />
        </ResultCard>
      </CardContainer>
    </Container>
  );
};

export default React.memo(ResultScreen);
